<template>
  <div>
    <div class="inf_scroll_wrapper" ref="scrollWrapper">
      <ul>
        <li v-for="(item, index) in tmp_list" :key="item.id" ref="picItem" :class="['pic_item', 'page-index' + item.pagerIndex]" @click="openPhotoSwipe(tmp_list, index)">
          <slot name="content" :item="item"></slot>
        </li>
        <li ref="picItemEnd" class="pic_item_end">{{endContent}}</li>
      </ul>
    </div>

    <div class="pswp" tabindex="-1" role="dialog" aria-hidden="true">

      <div class="pswp__bg"></div>

      <div class="pswp__scroll-wrap">

          <div class="pswp__container">
              <div class="pswp__item"></div>
              <div class="pswp__item"></div>
              <div class="pswp__item"></div>
          </div>

          <div class="pswp__ui pswp__ui--hidden">

              <div class="pswp__top-bar">

                  <div class="pswp__counter"></div>

                  <button class="pswp__button pswp__button--close" title="Close (Esc)"></button>

                  <div class="pswp__preloader">
                      <div class="pswp__preloader__icn">
                        <div class="pswp__preloader__cut">
                          <div class="pswp__preloader__donut"></div>
                        </div>
                      </div>
                  </div>
              </div>

              <div class="pswp__share-modal pswp__share-modal--hidden pswp__single-tap">
                  <div class="pswp__share-tooltip"></div>
              </div>

              <button class="pswp__button pswp__button--arrow--left" title="Previous (arrow left)">
              </button>

              <button class="pswp__button pswp__button--arrow--right" title="Next (arrow right)">
              </button>

              <div class="pswp__caption">
                  <div class="pswp__caption__center"></div>
              </div>

          </div>

      </div>

      <div v-show="canLoadImg" class="_pswp__bottom-bar">
        <div class="_pswp_load" @click="loadImg()">查看原图</div>
      </div>

    </div>

  </div>
</template>

<script>
import PhotoSwipe from 'photoswipe'
import PhotoSwipeUIDefault from 'photoswipe'
// import throttle from 'lodash/throttle'
// import debounce from 'lodash/debounce'
// import store from '@/store/index'

export default {
  name: 'photosScroll',
  props: {
    data: Array,
    currentIndex: Number,
    visible: Boolean,
    pageIndex: Number,
    loadFun: Function,
    loadParams: Object,
    pagerKey: String,
    imgKey: String,
    originImgKey: String
  },
  computed: {
  },
  data () {
    return {
      list: [],
      tmp_list: [],
      curPage: 0,
      isLoading: false,
      endContent: '加载中...',
      gallery: {},
      canLoadImg: true
    }
  },
  watch: {
  },
  mounted () {
    this.curPage = this.loadParams[this.pagerKey]

    this.loadList()

    window.onscroll = () => {
      this._scroll()
    }
  },
  methods: {

    // 调用加载方法
    async loadList () {
      if (this.isLoading) {
        return false
      }

      this.endContent = '加载中...'

      let params = this.loadParams
      params[this.pagerKey] = this.curPage

      this.isLoading = true

      let res = {}

      // 判断缓存中是否有已读列表
      if (this.list[params[this.pagerKey]]) {
        res.data = this.list[params[this.pagerKey]]
      } else {
        res = await this.loadFun(params)
      }

      if (!res || !res.data) {
        return false
      }

      if (res.data.length === 0) {
        this.endContent = '没有更多了'
        return false
      }

      for (let index = 0; index < res.data.length; index++) {
        res.data[index].pagerIndex = this.curPage
      }

      this.list[this.curPage] = res.data

      // this.tmp_list = res.data
      this.tmp_list = this.tmp_list.concat(res.data)

      this.curPage++

      this.isLoading = false
      this.scrollRender()
    },
    // 无限滚动 render
    scrollRender () {
      this._scroll()
    },
    // _scroll: debounce(function () {
    //   this.scroll()
    // }, 200),
    _scroll: function () {
      this.scroll()
    },
    // 滚动执行
    async scroll () {
      if (this.$refs.scrollWrapper) {
        let endItem = this.$refs.scrollWrapper.querySelector('.pic_item_end')

        if (!eleUnvisible(endItem)) {
          await this.loadList()
        }
      }
    },
    openPhotoSwipe (items, index) {
      items.forEach((item) => {
        function getNatural (src) {
          var image = new Image()
          image.src = src
          var naturalWidth = image.width
          var naturalHeight = image.height
          return {width: naturalWidth, height: naturalHeight}
        }

        item.src = item[this.imgKey]
        item.originSrc = item[this.originImgKey]

        let natural = getNatural(item.src)

        let curHeight = natural.height
        let curWidth = natural.width

        if (curHeight > curWidth) {
          curWidth = 450 * curWidth / curHeight
          curHeight = 450
        } else {
          curHeight = 375 * curHeight / curWidth
          curWidth = 375
        }

        item.w = curWidth
        item.h = curHeight
      })

      let pswpElement = document.querySelectorAll('.pswp')[0]
      // define options (if needed)
      let options = {
        // history & focus options are disabled on CodePen
        history: false,
        focus: false,
        index: index,
        showAnimationDuration: 0,
        hideAnimationDuration: 0

      }
      this.gallery = new PhotoSwipe(pswpElement, PhotoSwipeUIDefault, items, options)
      this.gallery.init()

      document.documentElement.style.overflow = 'hidden'

      this.gallery.listen('close', () => {
        document.documentElement.style.overflow = 'auto'
      })

      this.gallery.listen('afterChange', () => {
        console.log(decodeURI(this.gallery.itemHolders[1].el.querySelector('img').src))
        console.log(decodeURI(this.gallery.currItem[this.originImgKey]))
        if (decodeURI(this.gallery.itemHolders[1].el.querySelector('img').src) === this.gallery.currItem[this.originImgKey]) {
          this.canLoadImg = false
        } else {
          this.canLoadImg = true
        }
      })
    },
    loadImg () {
      // store.dispatch('operateLoader', 1)

      this.gallery.itemHolders[1].el.querySelector('img').onload = function () {
        // store.dispatch('operateLoader', -1)
      }

      this.gallery.itemHolders[1].el.querySelector('img').src = this.gallery.currItem[this.originImgKey]

      this.gallery.items[this.gallery.itemHolders[1].index].src = this.tmp_list[this.gallery.itemHolders[1].index][this.imgKey] = this.tmp_list[this.gallery.itemHolders[1].index][this.originImgKey]
      this.gallery.updateCurrItem()
      this.canLoadImg = false
    }
  }
}

function isScrollY () {
  return document.documentElement.offsetHeight > document.documentElement.clientHeight
}

function getScrollWidth () {
  var ele = document.createElement('div')
  ele.style.cssText = 'position:absolute;width:100px;height:100px;overflow:scroll;left:-1000px'
  document.body.appendChild(ele)
  var offsetWidth = ele.offsetWidth
  var clientWidth = ele.clientWidth
  var scrollWidth = offsetWidth - clientWidth
  document.body.removeChild(ele)
  return scrollWidth
}

function eleUnvisible (ele) {
  var getBoundingClientRect = ele.getBoundingClientRect()
  var scrollWidthY = isScrollY() ? getScrollWidth() : 0
  if (getBoundingClientRect.bottom < -500) {
    return true
  }
  if (getBoundingClientRect.top > (window.innerHeight - scrollWidthY + 500)) {
    return true
  }
  return false
}
</script>

<style scoped>

.pic_item{
  width: 1.23rem;
  height: 1rem;
  display: inline-block;
  margin: .01rem;
  background-color: #f7f7f7;
  background-position: center;
  background-size: cover;
}
.pswp__counter{
  opacity: 1;
  left: initial;
  right: 0;
}

.pswp__button{
  opacity: 1;
}

._pswp__bottom-bar{
  position: absolute;
  bottom: .5rem;
  text-align: center;
  width: 100%;
  z-index: 9999;
}

._pswp_load{
  color: #ffffff;
  width: 1rem;
  height: .32rem;
  border: 1px solid #fff;
  text-align: center;
  line-height: .32rem;
  border-radius: .05rem;
  margin: auto;
}

.pic_item_end{
  line-height: .2rem;
  text-align: center;
  padding: .1rem 0;
}

.pswp__button--close{
  float: left;
}
</style>



